import React from "react";

function Filter(props) {
  return (
    <div className="filter">
      <div className="result">  : {props.count} </div>
      <div className="sort">
        <div className="sort-title">   </div>
        <div className="form-checkbox">
          <div className="form-group">
            <input
            id="x"
              type="radio"
              value="asc"
              name="radiovalues"
              onChange={props.sortProducts}
            />
            <label htmlFor="x">  </label>
          </div>

          <div className="form-group">
            <input
            id="y"
              type="radio"
              value="desc"
              name="radiovalues"
              onChange={props.sortProducts}
            />
            <label htmlFor="y">  </label>
          </div>
        </div>
      </div>
      <label htmlFor="w" className="brand">
        
        <select id="w" onChange={props.filterProducts} value={props.brand}>
          <option value=""></option>
          <option value="mac">  </option>
          <option value="asus1"></option>
          <option value="hp"> </option>
          <option value="msi">   </option>
          <option value="apple"></option>
        </select>
      </label>
    </div>
  );
}

export default Filter;
